<template>
  <div id="add-task">
    <button type="button" @click="showTaskModal">添加任务</button><br>
    <div class="task-modal" v-if="isshowTaskModal">
      <h4>添加任务</h4>
      <span class="close" @click="showTaskModal">x</span>
      <div>
        <label>计划时间: </label>
        <input type="text" v-model="task.time">
      </div>
      <div>
        <label>任务名称: </label>
        <input type="text" v-model="task.name">
      </div>
      <div>
        <label>开始时间: </label>
        <input type="text" v-model="task.startTime">
      </div>
      <div>
        <label>结束时间: </label>
        <input type="text" v-model="task.endTime">
      </div>
      <div>
        <label>备注信息: </label>
        <input type="text" v-model="task.memo">
      </div>
      <button type="button" @click="saveTask">保存</button>
    </div>
  </div>
</template>
<script>
  import ajax from '../js/ajax';
  import Dialog from './Dialog';
  export default {
    data() {
      return {
        isshowTaskModal: false,
        task: {
          time: '',
          name: '',
          startTime: '',
          endTime: '',
          memo: ''
        }
      }
    },
    methods: {
      showTaskModal() {
        this.isshowTaskModal = !this.isshowTaskModal;
        this.task = {
          time: '',
          name: '',
          startTime: '',
          endTime: '',
          memo: ''
        }
      },
      saveTask() {
        if (!this.task.name) {
          alert('Please enter task name!');
          return;
        }
        let taskInfo = {
          time: this.task.time,
          name: this.task.name,
          startTime: this.task.startTime,
          endTime: this.task.endTime,
          memo: this.task.memo
        };
        ajax.save(taskInfo, (data) => {
          if (data.result) {
              console.log('save successfully.');
              this.showTaskModal();
              this.$emit('save-task-done');
          };
        });
      }
    },
    props: ['updateTaskList', 'Dialog']
  }
</script>
<style lang="stylus" scope>
  .task-modal
    position absolute
    left 50%
    top 50%
    transform translate(-50%, -50%)
    background gray
    width 250px
    height 220px
    overflow hidden
    .close
      position absolute
      top 5px
      right 5px
      cursor pointer
      font-size 20px
</style>



