import CONFIG from './config';
import Vue from 'vue';
import VueResource from 'vue-resource';
Vue.use(VueResource);

export default {
  fetch(filter, callback) {
    let filterStr = '';
    for (let key in filter) {
      filterStr += ('&' + key + '=' + filter[key]);
    }
    Vue.http.get(CONFIG.HOST + '/getTask?' + filterStr).then((response) => {
      callback && callback(response.data);
    });
  },
  save(item, callback) {
    if ('object' !== typeof item) {
      alert('Please save taskList is a object.');
      return;
    }
    Vue.http.post(CONFIG.HOST + '/saveTask', item, {emulateJSON: true}).then((response) => {
      callback && callback(response.data);
    });
  },
  update(item, callback) {
    Vue.http.post(CONFIG.HOST + '/updateTask', item, {emulateJSON: true}).then((response) => {
      callback && callback(response.data);
    });
  },
  delete(item, callback) {
    Vue.http.post(CONFIG.HOST + '/deleteTask', {_id: item}, {emulateJSON: true}).then((response) => {
      callback && callback(response.data);
    });
  }
}



