<template>
  <div id="task-list" class="task-list">
    <div id="mask" v-if="loading">
      <div class="box">
        <div class="text">请等待...</div>
      </div>
    </div>
    <div class="task-table-box">
      <h2>欢迎，记录您的任务便于享受更好的功能</h2>
      <div class="tool-box">
        <button type="button" @click="showAddModal">添加</button>
        <button type="button" @click="editTask">{{editOrSave}}</button>
        <button type="button" @click="delTask">删除</button>
        <span>显示：</span>
        <button :class="{push: taskTypePlan}" type="button" @click="showTaskList('plan')">计划</button>
        <button :class="{push: taskTypeDoing}" type="button" @click="showTaskList('doing')">进行</button>
        <button :class="{push: taskTypeDone}" type="button" @click="showTaskList('done')">完成</button>
        <span>优先级：</span>
        <select v-model="levelVal">
          <option value="">全部</option>
          <option v-for="item in level" :value="item.value">{{item.text}}</option>
        </select>
      </div>
      <div class="task-table">
        <div class="task-tr">
          <span class="task-td" v-for="item in taskListTitle"><span>{{item}}</span></span><!-- 表头信息 -->
        </div>
        <div :class="['task-tr', {active: index === active}]" v-for="(item, index) in taskList" @click="selectTr(item, index)">
          <span class="task-td">
            <span v-if="index !== active || !isEdit" :title="item.type">{{item.type}}</span>
            <input type="text" v-if="isEdit && index === active" v-model="item.type" class="task-input" @keyup.13="updateTask(item)">
          </span>
          <span class="task-td">
            <span v-if="index !== active || !isEdit" :title="item.name">{{item.name}}</span>
            <input type="text" v-if="isEdit && index === active" v-model="item.name" class="task-input" @keyup.13="updateTask(item)">
          </span>
          <span class="task-td">
            <span v-if="index !== active || !isEdit" :title="item.level">{{item.level}}</span>
            <input type="text" v-if="isEdit && index === active" v-model="item.level" class="task-input" @keyup.13="updateTask(item)">
          </span>
          <span class="task-td">
            <span v-if="index !== active || !isEdit" :title="item.memo">{{item.memo}}</span>
            <input type="text" v-if="index === active && isEdit" v-model="item.memo" class="task-input" @keyup.13="updateTask(item)">
          </span>
          <span class="task-td">
            <span v-if="index !== active || !isEdit" :title="format(item.planTime)">{{format(item.planTime, 'mm-dd')}}</span>
            <v-time v-if="isEdit && index === active" :data="item.planTime" :format="'mm-dd'" @change="getPlanTime($event, item)"></v-time>
            <!--input type="text" v-if="isEdit && index === active" v-model="item.planTime" class="task-input" @keyup.13="updateTask(item)"-->
          </span>
          <span class="task-td">
            <span v-if="item.startTime" :title="format(item.startTime)">{{format(item.startTime)}}</span>
            <span v-if="!item.startTime"><button @click="updateTime('startTime', item)">开始</button></span>
          </span>
          <span class="task-td">
            <span v-if="item.endTime" :title="format(item.endTime)">{{format(item.endTime)}}</span>
            <span v-if="!item.endTime"><button @click="updateTime('endTime', item)">结束</button></span>
          </span>
        </div>
        <div class="task-tr" v-if="isAddTask">
          <span class="task-td"><input v-model="task.type" class="task-input" type="text" @keyup.13="saveTask"></span><!-- type -->
          <span class="task-td"><input v-model="task.name" class="task-input" type="text" @keyup.13="saveTask"></span><!-- name -->
          <span class="task-td"><input v-model="task.level" class="task-input" type="text" @keyup.13="saveTask"></span><!-- level -->
          <span class="task-td"><input v-model="task.memo" class="task-input" type="text" @keyup.13="saveTask"></span><!-- memo -->
          <span class="task-td"><v-time :data="task.planTime" :format="'mm-dd'" @change="getPlanTime($event, task)"></v-time></span><!-- planTime -->
          <span class="task-td"></span><!-- startTime -->
          <span class="task-td"></span><!-- endTime -->
          <span class="task-td task-btn-box"><button type="button" @click="saveTask">保存</button></span>
        </div>
      </div>
      <!--<div class="task-page">
        <div class="page-content">
          <div class="page-info">
            当前 {{curPageNumber + 1}} 页 / 共 {{pageNumber}} 页
          </div>
          <ul class="page-detail">
            <li v-for="index in pageCountArr" @click="goToPage(index)">{{index + 1}}</li>
          </ul>
        </div>
      </div>
    </div>-->
  </div>
</template>
<script>
  import Ajax from '../js/ajax';
  import Time from '../js/time';
  import CONFIG from '../js/config';
  import AddTask from './AddTask';
  import vTime from './vTime';
  export default {
    data() {
      return {
        taskType: [],
        taskTypePlan: true,
        taskTypeDoing: false,
        taskTypeDone: false,
        loading: false,
        levelVal: '',
        level: [{
          text: '高',
          value: CONFIG.TASK.LEVEL.HIGH
        }, {
          text: '中',
          value: CONFIG.TASK.LEVEL.MID
        }, {
          text: '低',
          value: CONFIG.TASK.LEVEL.LOW
        }],
        taskListTitle: ['分类', '任务描述', '优先级', '备注',
          '计划完成', '开始时间', '结束时间'],
        taskList: [],
        task: {},
        isAddTask: false,
        isShowTips: false,
        active: -1,
        isEdit: false,
        editOrSave: '编辑',
        pageNumber: 1,
        curPageNumber: 0,
        offset: 0,
        pageCountArr: []
      }
    },
    methods: {
      showTaskList(type) {
        if (type === 'plan') {
          this.taskTypePlan = !this.taskTypePlan;
        } else if (type === 'doing') {
          this.taskTypeDoing = !this.taskTypeDoing;
        } else {
          this.taskTypeDone = !this.taskTypeDone;
        }
        this.taskType = [this.taskTypePlan ? '0' : '_', this.taskTypeDoing ? '1' : '_', this.taskTypeDone ? '2' : '_'];
        this.getTaskList();
      },
      // 更新表格信息
      getTaskList() {
        let filter = {
          level: this.levelVal,
          progress: this.taskType.join(''),
          offset: this.offset
        };
        this.loading = true;
        this.pageCountArr = [];
        Ajax.fetch(filter, result => {
          this.loading = false;
          // 前端过滤后显示
          if (result.success) {
            this.taskList = result.data;
            // 处理表格页码
            this.pageNumber = Math.ceil(result.other.pageCount / 10);
            for (let i = 0; i < this.pageNumber; i++) {
              this.pageCountArr.push(i);
            }
          }
        });
      },
      // 时间格式转换函数
      format: Time.format,
      /**
       * 选中当前行时，显示编辑按钮，显示详细信息
       */
      selectTr(item, index) {
        if (this.active !== index) {
          this.isEdit = false;
          this.editOrSave = '编辑';
        }
        this.active = index;
      },
      // 表格页码操控
      goToPage(index) {
        this.curPageNumber = index;
        this.offset = (index) * 10;
        this.getTaskList();
      },
      showAddModal() {
        this.isAddTask = !this.isAddTask;
      },
      delTask(item) {
        if (this.active === -1) {
          alert('请选择一条数据');
          return;
        }
        var item = this.taskList[this.active];
        this.loading = true;
        Ajax.delete(item._id, result => {
          this.loading = false;
          if (result.success) {
            // 手动移除DOM，不请求task列表
            for (let i = 0; i < this.taskList.length; i++) {
              if (this.taskList[i]._id === item._id) {
                this.taskList.splice(i, 1);
                this.active = -1; // 删除成功取消选中的行
                return;
              }
            }
          }
        });
      },
      editTask() {
        if (this.active === -1) {
          alert('请选择一条数据');
          return;
        }
        var item = this.taskList[this.active];
        if (this.isEdit) {
          this.isEdit = false;
          this.editOrSave = '编辑';
          // 更新数据
          this.updateTask(item);
        } else {
          this.isEdit = true;
          this.editOrSave = '更新';
        }
      },
      updateTask(item) {
        this.loading = true;
        Ajax.update(item, result => {
          this.loading = false;
          if(result.success) {
            this.active = -1;
            console.log('更新成功');
          }
        });
      },
      updateTime(type, item) {
        let time = new Date().getTime();
        let task = { _id: item._id };
        if ('startTime' === type) {
          task.endTime = item.endTime;
        } else {
          task.startTime = item.startTime;
        }
        task[type] = time;
        this.loading = true;
        Ajax.update(task, result => {
          this.loading = false;
          if (result.success) {
            if ('startTime' === type) {
              item.startTime = time;
            } else {
              item.endTime = time;
            }
          }
        });
      },
      saveTask() {
        if (!this.task.name) {
          alert('Please enter task name!');
          return;
        }
        let taskInfo = {
          type: this.task.type || '',
          name: this.task.name || '',
          level: this.task.level || '',
          planTime: this.task.planTime || '',
          memo: this.task.memo || '',
          startTime: this.task.startTime || '',
          endTime: this.task.endTime || ''
        };
        this.loading = true;
        Ajax.save(taskInfo, result => {
          console.log(result.data[0]);
          this.loading = false;
          if (result.success) {
              console.log('save successfully.');
              this.task = {}; // 清空数据
              this.showAddModal();
              // 添_id值
              taskInfo._id = result.data[0] ? result.data[0]._id : '';
              this.taskList.push(taskInfo);
          };
        });
      },
      getPlanTime(time, item) {
        item.planTime = time;
      }
    },
    created() {
      // 首次进来读取过滤条件
      this.taskType = [this.taskTypePlan ? '0' : '_', this.taskTypeDoing ? '1' : '_', this.taskTypeDone ? '2' : '_'];
      this.getTaskList();
    },
    components: {
      AddTask,
      vTime
    },
    watch: {
      progressVal() {
        this.getTaskList();
      },
      levelVal() {
        this.getTaskList();
      }
    }
  }
</script>
<style lang="stylus" scope>
  @import '../style/variable.styl'
  .task-table-box
    width tableW
    margin 0 auto
    text-align center
    .task-page
      margin-top 10px
      height 40px
      .page-content
        float right
        .page-info
          float left
          font-size 1.1em
          margin-right 10px
          line-height @height
        .page-detail
          float left
          height @height
          li
            float left
            width 30px
            height 30px
            line-height 30px
            margin 5px 5px 0 0
            background basicColor
            cursor pointer
  .task-table
    width tableW
    text-align left
    cursor default
    // height 440px
    // overflow hidden
    .task-btn-box
      position absolute
    .task-tr
      height tableTrH
      line-height tableTrH
      overflow hidden
      font-size 0 // 解决inline-block换行符导致的间隙
      &:nth-child(1)
        background basicColor
        font-weight bold
        text-align center
        border-top 1px solid #999
        border-bottom 1px solid #999
      &:nth-child(2n+2)
        background lightenColor
      &:nth-child(2n+3)
        background mostLightenColor
      &.active
        background tableActiveColor
      .task-td
        font-size basicFontSize // 14px
        padding 0 10px
        display inline-block
        vertical-align top
        white-space nowrap
        overflow hidden
        text-overflow ellipsis
        .task-input
          width 100%
          height @height - 10px
        &:nth-child(1)
          width @width * .11 // 取table的宽度的百分比
        &:nth-child(2)
          width @width * .37
        &:nth-child(3)
          width @width * .07
        &:nth-child(4)
          width @width * .15
        &:nth-child(5), &:nth-child(6), &:nth-child(7)
          width @width * .10
          text-align center
</style>



