<template>
  <div id="v-time">
    <input class="time-input" type="text" v-model="timeStr"
      @focus="recordTime" @blur="testTime">
  </div>
</template>
<script>
  import Time from '../js/time';
  export default {
    data() {
      return {
        oldTimeStr: '',
        time: this.data,
        timeStr: ''
      };
    },
    methods: {
      recordTime() {
        this.oldTimeStr = Time.format(this.time, this.format);
      },
      testTime() {
        // 非正常格式，返回上次数据，并报错
        if (Time.testTime(this.timeStr, this.format)) {
          // 进行格式转换，保存时间戳
          this.time = Time.arcFormat(this.timeStr, this.format);
          this.$emit('change', this.time);
        } else {
          this.timeStr = this.oldTimeStr;
        }
      }
    },
    watch: {
      data(val) {
        // 父组件改变，子组件更新
        this.time = val;
      },
    },
    props: ['data', 'format'],
    created() {
      // 传入ms时间戳，进行转换显示，确保input框内是mm-dd hh:mm格式
      this.timeStr = Time.format(this.time || new Date().getTime(), this.format);
      if (!this.time) {
        this.time = Time.arcFormat(this.timeStr, this.format);
        this.$emit('change', this.time);
      }
    }
  }
</script>
<style lang="stylus" scope>
  .time-input
    width 100%
    height 30px
</style>


