function addZero(value) {
  return value < 10 ? '0' + value : value;
}
export default {
  format(time, type) {
    let d = new Date(parseInt(time)),
      month = d.getMonth() + 1,
      date = d.getDate(),
      hour = d.getHours(),
      minute = d.getMinutes();
    if (type === 'mm-dd') {
      return `${addZero(month)}-${addZero(date)}`;
    } else {
      return `${addZero(month)}-${addZero(date)} ${addZero(hour)}:${addZero(minute)}`;
    }
  },
  testTime(time, type) {
    let reg = type === 'mm-dd' ? '^(0[1-9]|1[0-2])-(0[1-9]|[1-2][0-9]|3[0-1])$'
      : '^(0[1-9]|1[0-2])-(0[1-9]|[1-2][0-9]|3[0-1]) ([0-1][0-9]|2[0-3]):[0-5][0-9]$';
    let format = RegExp(reg);
    if (format.test(time)) {
      return true;
    } else {
      return false;
    }
  },
  arcFormat(time, type) {
    if (type === 'mm-dd') {
      // 默认修复时间为当天23:59:59
      let fixTime = 24 * 3600 * 1000 - 1;
      let timeStr = new Date(`2017/${time.split('-')[0]}/${time.split('-')[1]}`).getTime() + fixTime;
      return timeStr;
    }
  }
}


